h1 {
  text-align: center;
}

.top {
  margin-top: 5%;
}

.main-center {
  margin:0 auto;
  margin-left: 45%;
}

.center {
  margin:0 auto;
  margin-left: 15%;
}

.margin-top {
    margin-top: 10px;
}

.youtube-container {
  overflow: hidden;
  /* width: 120%; */
  /* Keep it the right aspect-ratio */
  aspect-ratio: 16/9;
  /* No clicking/hover effects */
  /* pointer-events: none; */
} 

.youtube-container iframe {
  /* Extend it beyond the viewport... */
  width: 100%;
  height: 100%;
  filter: brightness(0%);
  /* ...and bring it back again */
  /* margin-left: -450%; */
}

.revealed{

  filter: brightness(100%)!important;

}

